import React, { Suspense, useEffect, useState } from "react";
import { Canvas } from "react-three-fiber";
import { TorusKnot, OrbitControls, useTexture } from "@react-three/drei";
import DatGui, { DatSelect } from "react-dat-gui";
import "react-dat-gui/dist/index.css";

const folder = "images/Matcaps/";
function Thing({ matcapIndex }) {
  const matcap = useTexture(`${folder}/${matcapIndex}`);    
  return (
    <TorusKnot args={[30, 10, 100]}>
      <meshMatcapMaterial matcap={matcap} />
    </TorusKnot>
  );
}
function MatCaps() {
  const [data, setData] = useState({ select: "1.png"});
  useEffect(() => {
    document.title = "MatCaps";
    return () => {
      document.title = "WebGlfun";
    };
  }, []);
  const handleUpdate = (newData) => {
    setData({ select: newData.select});    
    console.log("handleUpdate");
  };
  

  return (
    <>
      <Canvas camera={{ position: [0, 0, -150] }}>
        <color attach="background" args={["black"]} />
        <OrbitControls />
        <Suspense fallback={null}>
          <Thing matcapIndex={data.select} />
        </Suspense>
      </Canvas>
      <div className="gui">
        <DatGui data={data} onUpdate={handleUpdate}>
          <DatSelect
            label="Select"
            path="select"
            options={["1.png", "2.png", "3.png"]}
          />
        </DatGui>
      </div>
    </>
  );
}

export default MatCaps;
